import React, { Component } from 'react';
import { NavBar, Icon, ActivityIndicator } from 'antd-mobile';
import Content from '../../views/common/Content';
import '../../assets/css/overview.css';

// import overviewList from '../../assets/config/overview.js';

class OverviewDetailScreen extends Component
{
    constructor(props)
    {
        super(props);
        this.state = {
            isFetching: false,
        };
    }
    componentDidMount()
    {
        this.loadData();
    }
    loadData()
    {
        import('../../assets/config/overview.js').then(({ overviewList })=>{
            let item = overviewList[this.props.match.params.index];
            this.setState({
                item,
                isFetching: false,
            });
        });
    }
    render()
    {
        // let index = this.props.match.params.index;
        // let item = overviewList[index];
        let item = this.state.item;
        return (
            <div>
                <NavBar mode="dark" 
                    className="navBar"
                    icon={<Icon type="left" />}
                    onLeftClick={() => {
                        this.props.history.goBack();
                    }}>
                    { item ? item.title : '' }
                </NavBar>
                <Content style={{ backgroundColor: '#efeff4' }}>
                    {
                        this.state.isFetching
                            ?
                            <ActivityIndicator 
                                toast
                                text="Loading..." />
                            :
                            <div className="overviewDetail">
                                {
                                    this.renderViews(item ? item.details : null)
                                }
                            </div>
                    }
                </Content>
            </div>
        );
    }
    renderViews(detailList)
    {
        if(!detailList || detailList.length === 0)
        {
            return null;
        }
        return detailList.map((item, index)=>{
            if(item.type === 'image')
            {
                return this.renderImageView(item.value, index);
            }
            else if(item.type === 'author')
            {
                return this.renderAuthorView(item.value, index);
            }
            else if(item.type === 'title')
            {
                return this.renderTitleView(item.value, index);
            }
            else if(item.type === 'subTitle')
            {
                return this.renderSubTitleView(item.value, index);
            }
            else if(item.type === 'content')
            {
                return this.renderContentView(item.value, index);
            }
            return null;
        });
    }
    renderImageView(value, index)
    {
        return <img src={value} alt='' key={index} className="overviewDetailImage"/>;
    }
    renderAuthorView(value, index)
    {
        return <div className="overviewDetailAuthor" key={index}>{ value }</div>;
    }
    renderTitleView(value, index)
    {
        return <div className="overviewDetailTitle" key={index}>{ value }</div>;
    }
    renderSubTitleView(value, index)
    {
        return <div className="overviewDetailSubTitle" key={index}>{ value }</div>;
    }
    renderContentView(value, index)
    {
        if(!value)
        {
            return;
        }
        if(typeof value === 'string')
        {
            return <div className="overviewDetailContent" key={index}>{ value }</div>;
        }
        else
        {
            return value.map((_value, _index)=><div className="overviewDetailContent" key={index + _index}>{ _value }</div>);
        }
    }
}

export default OverviewDetailScreen;


